
*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Outfit', sans-serif;
}
body
{
    background: #000;
}
h1,
h4,
p,
a
{
    color: #fff;
    text-decoration: none;
}
ul
{
    list-style: none;
}
.btn
{
    padding: 12px 32px;
    font-size: 1rem;
    text-transform: uppercase;
    background:  #7f7f7f;
    color: #222;
    border: 1px solid #fff;
    font-weight: 600;
    cursor: pointer;
}
.btn-light
{
    background: transparent;
    color: #fff;
}
.btn:hover
{
    background: rgba(255, 255, 255, 0.2);
    color: #fff;
    transition: 0.20s;
}
.Counter
{
    color: #fff;
}
